import React, { useState, useMemo } from 'react'
import { Form, Button, Selector, Input, DatePicker, Result } from 'antd-mobile'
import './App.css';
import { TIME_INTERVAL_ENUMS, METHODS } from './ constants'
import moment from 'moment'
import { SmileOutline, FrownOutline } from 'antd-mobile-icons'

const PLACEHOLDER_ENUMS = {
  st: '',
  et: ''
}

const App = () => {
  const [showCustomInput, setShowCustomInput] = useState(false)
  const [visible, setVisible] = useState(false)
  const [currentPlaceholder, setCurrentPlaceHolder] = useState(PLACEHOLDER_ENUMS['st'])
  const [selectedMethod, setSelectedMethod] = useState('st')
  const [result, setResult] = useState()
  const [frown, setFrown] = useState()
  const [color, setColor] = useState('#1677ff')

  const [form] = Form.useForm()


  const onFinish = () => {
    form.validateFields().then(() => {
      const { interval, customInterval = 0, method, time } = form.getFieldsValue()
      const _time = moment(time).format('YYYY-MM-DD HH:mm:ss')
      const _method = method[0]
      let _interval = +interval[0]

      // 
      if (_interval === 0) {
        _interval = customInterval
      }

      // 
      if (_method === 'st') {
        _interval = ~_interval + 1
      }

      try {
        setFrown(false)
        setColor('#76c6b8')
        setResult(moment(_time).subtract(_interval, 'hour').format('YYYY-MM-DD HH:mm:ss'))
      } catch (e) {
        setColor('#ff3141')
        setResult(undefined)
        setFrown(true)
      }
    })
  }

  const handleIntervalChange = (val) => {
    const newVal = val[0]
    if (newVal === 0) {
      form.setFieldsValue({
        customInterval: undefined
      })
      setShowCustomInput(true)
      return
    }

    setShowCustomInput(false)
  }

  const handleMethodChange = (val) => {
    setCurrentPlaceHolder(PLACEHOLDER_ENUMS[val])
    setSelectedMethod(val)
  }

  const description = useMemo(() => {
    if (frown) {
      return ''
    }

    if (!result) {
      return ''
    }

    const { method, time } = form.getFieldsValue()
    const _time = moment(time).format('YYYY-MM-DD HH:mm:ss')
    const _method = method?.[0]

    const defalutText = ''

    let desc = `${defalutText}${_time} - ${result}`

    if (_method === 'et') {
      desc = `${defalutText}${result} - ${_time}`
    }

    return desc
  }, [form, frown, result])

  const checkCustomInterval = (_, val) => {
    const num = +val

    if (val && (isNaN(num) || typeof num !== 'number')) {
      return Promise.reject(new Error(''))
    }

    return Promise.resolve()
  }


  return (
    <div className="App">
      <div style={{
        backgroundColor: '#1677ff',
        fontSize: '20px',
        color: '#fff',
        padding: '18px',
        fontWeight: 'bold'
      }}></div>

      <div>
        <Form
          form={form}
          initialValues={{ interval: [72], method: ['st'] }}
          onFinish={onFinish}
          footer={
            <Button block type='submit' color='primary' size='large'>
              
            </Button>
          }
          mode='card'
        >
          <Form.Item name='interval' label='' rules={[{ required: true, message: '' }]}>
            <Selector
              columns={2}
              options={TIME_INTERVAL_ENUMS}
              onChange={handleIntervalChange}
            />
          </Form.Item>
          {
            showCustomInput ? <Form.Item
              name='customInterval'
              label=''
              rules={[{ required: true }, { validator: checkCustomInterval }]}>
              <Input placeholder='24' />
            </Form.Item> : null
          }
          <Form.Item name='method' label='' rules={[{ required: true, message: '' }]}>
            <Selector
              columns={2}
              options={METHODS}
              onChange={handleMethodChange}
            />
          </Form.Item>
          {
            selectedMethod ? <Form.Item
              name='time'
              label={currentPlaceholder}
              onClick={() => setVisible(true)}
              trigger="onConfirm"
              rules={[{ required: true, message: '' }]}>
              <DatePicker
                visible={visible}
                onClose={() => {
                  setVisible(false)
                }}
                precision='minute'
              >
                {value =>
                  value ? moment(value).format('YYYY-MM-DD HH:mm:ss') : `${currentPlaceholder}`
                }
              </DatePicker>
            </Form.Item> : null
          }
        </Form>
      </div>
      <div>
        <Result
          icon={frown ? <FrownOutline style={{ color }} /> : <SmileOutline style={{ color }} />}
          status='success'
          title={result || ''}
          description={description}
        />
      </div>
    </div>
  );
}

export default App;
